  font-weight: bold;
  font-size: 2rem;
  border: 3px solid #f1f1f1;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 100%;
  padding: 1rem;
  text-align: center;
  max-width: 900px;
  transition: 200ms all ease-in-out;
}

.home_name__WfHCE {
